import _ from "lodash";
import store from "store";

const sampleViviendas = [
  {
    "id": 1,
    "provincia": "Buenos Aires", 
    "departamento": "San Martín",
    "calle":"Ricochet",
    "altura": "1420",
    "piso": "PB",
    "depto": "b",
    "descripcion": "rejas negras"
  },
  {
    "id": 2,
    "provincia": "CABA", 
    "calle":"sarmiento",
    "altura": "365"
  }
]; 

const getSampleViviendaById = (id) => {
  return _.find(sampleViviendas, {'id': parseInt(id)});
};


export const getViviendas = () => {
  return new Promise((resolve, reject) => {
    resolve(sampleViviendas);
  })
}

export const getViviendaById = (id) => {
  return new Promise((resolve, reject) => {
    console.log(id);
    var vivienda = getSampleViviendaById(id);
    console.log(vivienda);
    if(vivienda) {
      resolve(vivienda);
    }
    else {
      reject("error");
    }
  })
}

export const getViviendaReal = (viviendaId) => {
  return new Promise ((resolve, reject) => {
    const result = store.get('real.' + viviendaId);
    resolve(result);
  });
}

export const setViviendaReal = (viviendaId, contacto) => {
  return new Promise ((resolve, reject) => {
    store.set('real.' + viviendaId, contacto);
    resolve();
  });
}


export const getViviendaContacto = (viviendaId) => {
  return new Promise ((resolve, reject) => {
    const result = store.get('contacto.' + viviendaId);
    resolve(result);
  });
}

export const setViviendaContacto = (viviendaId, contacto) => {
  return new Promise ((resolve, reject) => {
    store.set('contacto.' + viviendaId, contacto);
    resolve();
  });
}

