import React, { useState } from 'react';
import { Col, Row, Card, Form, Button } from '@themesberg/react-bootstrap';
import PrettyPrint from './PrettyPrint';
import { useFormState } from 'react-use-form-state';
import provincias from './provincias';
import { useParams } from "react-router-dom";
import { getViviendaById, getViviendaContacto, setViviendaContacto } from "../services/ViviendaService";
import usePromise from "react-fetch-hook/usePromise";
import { Redirect } from 'react-router-dom/cjs/react-router-dom.min';
import { RouteBuilder } from "../routes";

const DatosContacto = ({vivienda, contacto}) => {
  const [formState, { text, email, textarea }] = useFormState(contacto || {});

  const [submitted, setSubmitted] = useState(false);

  const submitDatos = (event) => {
    setViviendaContacto(vivienda.id, formState.values);
    alert("dead end");
    event.preventDefault();
  }

  const back = (event) => {
    setViviendaContacto(vivienda.id, formState.values);
  }
  if(submitted) {
    //return <Redirect to={RouteBuilder.viviendaContacto(row.id)} />
  }

  return (
    <Form onSubmit={submitDatos}>

      <Card border="light" className="bg-white shadow-sm mb-4">
      <Card.Body>
        <h5>Datos contacto</h5>
        <Row>
          <Col md={4} className="mb-3">
            <Form.Group className="mb-3">
              <Form.Label>Nombre de pila</Form.Label>
              <Form.Control {...text('nombre_pila')} />
            </Form.Group>
          </Col>
          <Col md={2} className="mb-3">
            <Form.Group className="mb-3">
              <Form.Label>Telefono fijo</Form.Label>
              <Form.Control {...text('telefono_fijo')} />
            </Form.Group>
          </Col>
          <Col md={2} className="mb-3">
            <Form.Group className="mb-3">
              <Form.Label>Celular</Form.Label>
              <Form.Control {...text('celular')} />
            </Form.Group>
          </Col>
          <Col md={2} className="mb-3">
            <Form.Group className="mb-3">
              <Form.Label>email</Form.Label>
              <Form.Control {...email('email')} />
            </Form.Group>
          </Col>
        </Row>
        <Row>
          <Col md={10} className="mb-3">
            <Form.Group className="mb-3">
              <Form.Label>Descripcion</Form.Label>
              <Form.Control as="textarea" rows="3" {...textarea('observaciones')}/>
            </Form.Group>
          </Col>
        </Row>
        </Card.Body>
      </Card>
      <Row>
        <Col md={1} className="mb-3">
          <Button variant="secondary" href={"#" + RouteBuilder.viviendaSingle(vivienda.id)} onClick={back}>Atras</Button>
        </Col>
        <Col md={3} className="mb-3">
          <Button variant="primary" type="submit">Siguiente</Button>
        </Col>
      </Row>
      <PrettyPrint formState={formState} />
    </Form>
  );
}

export default () => {
  const { id } = useParams();
  const { isLoading, data } = usePromise(async () => {
    const vivienda = await getViviendaById(id);
    const contacto = await getViviendaContacto(id);
    return {vivienda, contacto};
  });



  return (
      <div>

          <h1 className="h2">Vivienda</h1>
          {isLoading ? "Loading..." : <DatosContacto vivienda={data.vivienda} contacto={data.contacto} />}
      </div>

  );
  
};
